import React from 'react'
import PropTypes from 'prop-types'
import CardCategory from 'questionnaire/components/CardCategory'
import Text from 'components/Text'
import { Container, Risk } from './styles'
import { CANCER_RISKS } from 'config/constants'

export default function CategoryResultItem ({ title, image, risk, ...props }) {
  const riskObj = CANCER_RISKS[risk]
  return (
    <Container {...props}>
      <CardCategory
        title={title}
        image={image}
      />
      <Risk>
        <Text size='12px' align='center'>{riskObj.name === 'Médio' ? 'Necessita' : 'Risco'}</Text>
        <Text size='18px' align='center' color={riskObj.color} lineHeight={1}>
          <strong>{riskObj.name === 'Médio' ? 'Realizar Exames' : riskObj.name}</strong>
        </Text>
      </Risk>
    </Container>
  )
}

CategoryResultItem.propTypes = {
  /** Título */
  title: PropTypes.string.isRequired,

  /** Imagem */
  image: PropTypes.string.isRequired,

  /** Risco de câncer */
  risk: PropTypes.oneOf(Object.keys(CANCER_RISKS)).isRequired
}



