import React, { Component } from 'react'
import { connect } from 'react-redux'
import { isEmpty } from 'ramda'
import Spinner from 'components/Spinner'
import Text from 'components/Text'
import Link from 'components/Link'
import CategoryResultItem from 'questionnaire/components/CategoryResultItem'
import * as duck from 'store/ducks/questionnaire'
import getRoute from 'utils/getRoute'
import routes from 'routes'
import VerticalSpacer from 'components/VerticalSpacer'

class Result extends Component {
  componentDidMount () {
    this.props.fetchResult({
      patientToken: this.props.patientToken,
      scriptHash: this.props.scriptHash
    })
  }

  render () {
    if (this.props.isSubmitting) {
      return <Spinner text='Carregando resultados' />
    }

    if (isEmpty(this.props.result)) {
      const to = getRoute(routes.questionnaire.categories, {
        patient: this.props.patientToken,
        script: this.props.scriptHash
      })

      return (
        <Text>
          Nenhum resultado encontrado.
          {' '}
          <Link to={to}>Clique aqui</Link> para responder às questões.
        </Text>
      )
    }

    return (
      <VerticalSpacer>
        <Text size='25px' style={{ fontWeight: 'bold' }}>
          Obrigado por realizar a avaliação!
        </Text>
        <Text>
          Enfim, finalizamos! Obrigado pelo seu tempo e as respostas dadas. O próximo passo é aguardar. Mas não se preocupe, retornaremos com resultado final em aproximadamente 72 horas.
        </Text>
        {this.props.result.map(categoria => (
          <CategoryResultItem
            key={categoria.id}
            title={categoria.nome}
            image={categoria.tipo_cancer.imagem ? categoria.tipo_cancer.imagem.url : ''}
            risk={categoria.risco}
          />
        ))}
      </VerticalSpacer>
    )
  }
}

const mapStateToProps = state => ({
  isSubmitting: duck.isSubmitting(state),
  patientToken: duck.getPatientToken(state),
  scriptHash: duck.getScriptHash(state),
  result: duck.getResult(state)
})

const mapDispatchToProps = {
  fetchResult: duck.fetchResult
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Result)



