import styled from 'styled-components'

export const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;

  & + & {
    border-top: 1px solid ${props => props.theme.colors.formBorder};
  }
`

export const Risk = styled.div`
  width: 100px;
  text-align: center;
`



