import React from 'react'
import Screen from 'components/Screen'
import PageContent from 'components/PageContent'
import Container from 'components/Container'
import VerticalSpacer from 'components/VerticalSpacer'
import Header from 'questionnaire/components/Header'
import Notifications from 'containers/Notifications'

export default function QuestionnaireScreen ({ personal, children, ...props }) {
  return (
    <Screen {...props}>
      <Header title={props.title} color={props.color} personal={personal} />
      <PageContent>
        <Container size='xsmall'>
          <VerticalSpacer>
            <Notifications />
            <div>
              {children}
            </div>
          </VerticalSpacer>
        </Container>
      </PageContent>
    </Screen>
  )
}



