import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { isEmpty } from 'ramda'
import { Redirect } from 'react-router-dom'
import Wizard from 'questionnaire/components/Wizard'
import * as forms from './forms'
import theme from 'theme'

export default class PatientWizard extends Component {
  constructor (props) {
    super(props)

    this.state = {
      activeForm: 0,
      done: false,
      formData: {}
    }

    this.forms = [
      forms.formPersonal,
      forms.formAddress,
      forms.formPhysical,
      forms.formEthnicity,
      forms.formExam,
      forms.formHistory
    ]

    const formFields = this.getFormFields()

    if (formFields.length > 0) {
      this.forms.push(formFields)
    }
  }

  getFormFields () {
    const formFields = []
    const { scriptFormFields } = this.props

    if (scriptFormFields.matricula) {
      formFields.push(forms.formCode)
    }

    if (scriptFormFields.telefone) {
      formFields.push(forms.formPhone)
    }

    if (scriptFormFields.plano_saude) {
      formFields.push(forms.formHealthInsurance)
    }

    return formFields
  }

  componentDidUpdate ({ isSubmitting }) {
    if (isSubmitting && !this.props.isSubmitting && isEmpty(this.props.error)) {
      this.setState({ done: true })
    }
  }

  handleSubmit = (values) => {
    const formData = {
      ...this.state.formData,
      ...values
    }

    if (this.state.activeForm < this.forms.length - 1) {
      this.setState({
        activeForm: this.state.activeForm + 1,
        formData
      })
    } else {
      this.setState({
        formData
      }, () => {
        this.props.onSubmit(formData)
      })
    }
  }

  handleOnBackClick = () => {
    if (this.state.activeForm > 0) {
      this.setState({
        activeForm: this.state.activeForm - 1
      })
    }
  }

  render () {
    if (this.state.done && this.props.redirectTo) {
      return <Redirect to={this.props.redirectTo} />
    }

    return (
      <Wizard
        initialValues={this.props.initialValues}
        onSubmit={this.handleSubmit}
        form={this.forms[this.state.activeForm]}
        count={this.forms.length}
        progress={this.state.activeForm + 1}
        canGoBack={this.state.activeForm > 0}
        onBackClick={this.handleOnBackClick}
        isSubmitting={this.props.isSubmitting}
        color={theme.colors.personal}
      />
    )
  }

  static propTypes = {
    /** Dados iniciais */
    initialValues: PropTypes.object.isRequired,

    /** Form Fields */
    scriptFormFields: PropTypes.object,

    /** Submit handler */
    onSubmit: PropTypes.func.isRequired,

    /** Formulando sendo processado */
    isSubmitting: PropTypes.bool.isRequired,

    /** Possível error */
    error: PropTypes.object
  }
}



