import React, { Component } from 'react'
import { Redirect } from 'react-router-dom'
import QuestionnaireScreen from 'questionnaire/components/QuestionnaireScreen'
import PatientWizard from 'questionnaire/components/PatientWizard'
import Resource from 'containers/Resource'
import routes from 'routes'
import getRoute from 'utils/getRoute'

export default class CompanyPatientScreen extends Component {
  state = {
    done: false,
    scriptHash: '',
    patientToken: ''
  }

  render () {
    if (this.state.done) {
      const to = getRoute(routes.questionnaire.index, {
        script: this.state.scriptHash,
        patient: this.state.patientToken
      })
      return <Redirect to={to} />
    }

    return (
      <QuestionnaireScreen title='Informações pessoais' personal>
        <Resource
          resource='AnamneseCompany'
          onSuccess={({ data }) => {
            this.setState({
              done: true,
              scriptHash: data.hash,
              patientToken: data.token
            })
          }}
        >
          {props => (
            <PatientWizard
              initialValues={{}}
              isSubmitting={props.isSubmitting}
              onSubmit={data => (
                props.create(this.props.match.params.hash, {
                  ...data,
                  empresa: {
                    id: this.props.match.params.companyId
                  }
                })
              )}
              error={props.error}
              scriptFormFields={{}}
            />
          )}
        </Resource>
      </QuestionnaireScreen>
    )
  }
}



