import React, { Component } from 'react'
import { isEmpty } from 'ramda'
import { Field } from 'react-final-form'
import VerticalSpacer from 'components/VerticalSpacer'
import FormGrid from 'components/FormGrid'
import CustomField from 'components/CustomField'
import CustomFieldSelect from 'admin/components/CustomFieldSelect'
import * as forms from 'utils/forms'
import withResource from 'store/utils/withResource'

class FormAddress extends Component {
  componentDidUpdate ({ values: { cep: previousCep }, ...prevProps }) {
    const { cep: currentCep = '' } = this.props.values
    const { resourceProps: resource } = this.props

    if (currentCep.length === 8 && currentCep !== previousCep) {
      return resource.fetchOne(currentCep)
    }

    if (prevProps.resourceProps.isFetching && !resource.isFetching) {
      const address = resource.detailedRecords[currentCep]
      if (address && !isEmpty(address) && isEmpty(resource.error)) {
        this.props.form.change('pais', address.pais)
        this.props.form.change('estado', undefined)
        this.props.form.change('estado', address.estado)
        this.props.form.change('cidade', address.cidade)
        this.props.form.change('rua', address.rua)
      }
    }
  }

  render () {
    const { isFetching: isLoadingAddress } = this.props.resourceProps
    return (
      <div>
        <VerticalSpacer>
          <FormGrid>
            <Field
              name='cep'
              label='CEP'
              id='form_address_cep'
              component={CustomField}
              disabled={isLoadingAddress}
              {...forms.cepRequired}
            />
            <Field
              name='pais.id'
              label='País'
              id='form_address_country'
              disabled
              component={CustomFieldSelect}
              customSelectProps={{
                options: this.props.values.pais
                  ? [ this.props.values.pais ]
                  : [],
                labelKey: 'nome',
                valueKey: 'id',
                isLoading: isLoadingAddress,
                placeholder: 'País'
              }}
              {...forms.required}
            />
          </FormGrid>
          <FormGrid>
            <Field
              name='estado.id'
              label='Estado'
              id='form_address_state'
              disabled
              component={CustomFieldSelect}
              customSelectProps={{
                options: this.props.values.estado
                  ? [ this.props.values.estado ]
                  : [],
                labelKey: 'nome',
                valueKey: 'id',
                isLoading: isLoadingAddress,
                placeholder: 'Estado'
              }}
              {...forms.required}
            />
            <Field
              name='cidade.id'
              label='Cidade'
              id='form_patient_city'
              disabled
              component={CustomFieldSelect}
              customSelectProps={{
                options: this.props.values.cidade
                  ? [ this.props.values.cidade ]
                  : [],
                labelKey: 'nome',
                valueKey: 'id',
                isLoading: isLoadingAddress,
                placeholder: 'Cidade'
              }}
              {...forms.required}
            />
          </FormGrid>
          <Field
            name='pais'
            component={CustomField}
            format={v => JSON.stringify(v)}
            disabled={isLoadingAddress}
            type='hidden'
          />
          <Field
            name='estado'
            component={CustomField}
            format={v => JSON.stringify(v)}
            disabled={isLoadingAddress}
            type='hidden'
          />
          <Field
            name='cidade'
            component={CustomField}
            format={v => JSON.stringify(v)}
            disabled={isLoadingAddress}
            type='hidden'
          />
          <Field
            name='rua'
            component={CustomField}
            label='Endereço'
            disabled={isLoadingAddress}
            {...forms.required}
          />
          <FormGrid>
            <Field
              name='numero'
              component={CustomField}
              label='Número'
              {...forms.required}
            />
            <Field
              name='complemento'
              component={CustomField}
              label='Complemento'
            />
          </FormGrid>
        </VerticalSpacer>
      </div>
    )
  }
}

export default withResource(FormAddress, {
  resource: 'Addresses'
})



