import React from 'react'
import QuestionnaireScreen from 'questionnaire/components/QuestionnaireScreen'
import Text from 'components/Text'
import Button from 'components/Button'
import routes from 'routes'
import { Link } from 'react-router-dom'
import * as duck from 'store/ducks/questionnaire'
import { connect } from 'react-redux'
import getRoute from 'utils/getRoute'

function AnamneseScreen ({ data, patientToken, scriptHash }) {
  const to = getRoute(routes.questionnaire.categories, {
    patient: patientToken,
    script: scriptHash
  })

  return (
    <QuestionnaireScreen title='Anamnese' personal>
      <Text
        size='25px'
        lineHeight={1}
        style={{marginBottom: '15px'}}
      >
        <strong>Olá, {data.paciente.name}</strong>
      </Text>
      <Text size='18px'>
        Chegou o momento de conhecermos você e um pouco do seu histórico médico. Preencha os campos de maneira precisa, para que a sua avaliação de risco seja feita da melhor forma. Vamos lá?
      </Text>
      <Button
        to={to}
        color='personal'
        style={{width: '100%', marginTop: '50px'}}
        component={Link}
      >
        Iniciar avaliação
      </Button>
    </QuestionnaireScreen>
  )
}

const mapStateToProps = state => ({
  data: duck.getData(state),
  patientToken: duck.getPatientToken(state),
  scriptHash: duck.getScriptHash(state)
})

const mapDispatchToProps = {

}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AnamneseScreen)



