import React from 'react'
import Section from 'admin/components/Section'
import VerticalSpacer from 'components/VerticalSpacer'
import Text from 'components/Text'

export default function Terms () {
  return (
    <Section title='Termos e Condições' wrapped={false}>
      <VerticalSpacer space={15}>
        <Text>
          <strong>Antes de começar: quem somos e o que fazemos</strong>
        </Text>
        <Text>
          A Previneo é uma empresa dedicada à promoção de informação voltada à prevenção de alguns tipos de câncer. Fazemos isso por acreditar que quanto maior o acesso à informação, maiores as chances de um diagnóstico precoce de câncer, o que é muitas vezes fundamental para um tratamento adequado <strong>e, principalmente, para aumentar a possibilidade de reduzir a morbidade e mortalidade.</strong>
        </Text>
        <Text>
          Não prestamos um serviço médico de diagnóstico e nem prescrevemos tratamentos. Nosso formulário foi embasado na literatura médica atualmente aceita, e leva em consideração a existência de alguns fatores de aumento de risco para desenvolvimento de alguns tipos de câncer.
        </Text>
        <Text>
          Para preencher nosso formulário, você precisa ler e concordar com algumas condições de uso. Sugerimos que, havendo dúvidas, você entre em contato conosco antes de aceitar nossas condições de uso (contato@previneo.com.br). Estamos à disposição para esclarecer quaisquer questões, e nosso objetivo é que você somente continue o preenchimento do formulário se realmente tiver lido, compreendido e estiver de acordo com nossas condições.
        </Text>
        <Text>
          <strong>Qual é nossa metodologia</strong>
        </Text>
        <Text>
          Nosso formulário foi pensado para calcular o risco de desenvolvimento de cinco tipos de câncer (câncer de mama, próstata, colo de útero, cólon e pulmão). A quantidade de perguntas realizadas dependerá do sexo do usuário. Pessoas do sexo masculino responderão ao questionário referente a três tipos de câncer (próstata, cólon e pulmão) e pessoas do sexo feminino a quatro tipos de câncer (mama, colo de útero, cólon e pulmão).
        </Text>
        <Text>
          A estratificação do risco de câncer (próstata, cólon, colo de útero e pulmão) leva em consideração fatores de risco já estabelecidos na literatura científica (http://www.inca.gov.br/estimativa/2014/estimativa-24042014.pdf). O risco aumentado para desenvolvimento de uma patologia é embasado na presença de pelo menos um fator de risco apontado no questionário preenchido.
        </Text>
        <Text>
          Por exemplo, pessoas que fumam tem maiores chances de desenvolver câncer de pulmão. Isso não significa que pessoas não fumantes não possam desenvolvê-lo, mas sim que o risco é menor.
        </Text>
        <Text>
          O risco relativo para o desenvolvimento do câncer de mama foi baseado na ferramenta de cálculo de risco denominada “risco de Gail”, embasada pelos seguintes artigos científicos:
        </Text>
        <Text>
          <ol>
            <li>
              Gail MH, Brinton LA, Byar DP, Corle DK, Green SB, Shairer C, Mulvihill JJ: Projecting individualized probabilities of developing breast cancer for white females who are being examined annually. J Natl Cancer Inst 81(24):1879-86, 1989.
            </li>
            <li>
              Costantino JP, Gail MH, Pee D, Anderson S, Redmond CK, Benichou J, Wieand HS: Validation studies for models projecting the risk of invasive and total breast cancer incidence. J Natl Cancer Inst 91(18):1541-8, 1999.
            </li>
            <li>
              Gail MH, Costantino JP, Bryant J, Croyle R, Freedman L, Helzlsouer K, Vogel V: Weighing the risks and benefits of tamoxifen treatment for preventing breast cancer. J Natl Cancer Inst 91(21):1829-46, 1999.
            </li>
            <li>
              Rockhill B, Spiegelman D, Byrne C, Hunter DJ, Colditz GA: Validation of the Gail et al. model of breast cancer risk prediction and implications for chemoprevention. J Natl Cancer Inst 93(5):358-66, 2001.
            </li>
          </ol>
        </Text>
        <Text>
          O “risco de Gail” é <strong>uma ferramenta</strong> reconhecida pela Sociedade Brasileira de Mastologia como adequada para verificação do risco de desenvolvimento do câncer de mama e <strong>aplicada em vários serviços de Mastologia.</strong>
        </Text>
        <Text>
          <strong>Calculando o risco</strong>
        </Text>
        <Text>
          Risco significa probabilidade de desenvolvimento de uma determinada patologia. Calcularemos o risco de acordo com as informações prestadas por você ao nosso formulário. Se não estiver presente nenhum fator de risco, seu risco será considerado normal, isto é, corresponde ao risco <strong>populacional</strong> existente para a seu sexo e faixa etária <strong>na data de preenchimento do formulário</strong>. Se houver algum fator de risco presente, isso fará com que seu risco seja considerado aumentado, ou seja, maior do que aquele existente para seu sexo e faixa etária <strong>(risco populacional médio)</strong>, na data de preenchimento do formulário.
        </Text>
        <Text>
          A ferramenta serve para o cálculo de risco relativo e, por se tratar de análise estatística, é passível de falsos negativos e falsos positivos. Isso significa que o fato de se ter risco normal não é indicativo de que você não irá desenvolver qualquer patologia, mas sim que seu risco é considerado normal, em comparação com suas condições de sexo e idade. Da mesma forma, o fato de se obter um resultado de risco aumentado não significa que você irá desenvolver determinada patologia, mas apenas que seu risco é maior, em comparação com suas condições de sexo e idade.
        </Text>
        <Text>
          Voltamos ao exemplo do risco de câncer de pulmão em relação a pessoas fumantes e não fumantes. Qualquer uma delas pode desenvolver patologia, mas fumantes têm risco maior em relação a não fumantes.
        </Text>
        <Text>
          Como a ferramenta é embasada da literatura médica atual, os resultados são estimados com <strong>precisão científica considerada satisfatória e somente podem ser interpretados na data de preenchimento do formulário</strong>. Eventuais desvios não podem ser considerados erros, pois fazem parte de qualquer análise estatística.
        </Text>
        <Text>
          <strong>A necessidade de complemento médico</strong>
        </Text>
        <Text>
          Divulgamos informações voltadas à estimativa de risco de desenvolvimento de uma patologia. Seja qual for o seu resultado, o preenchimento do formulário não substitui visitas regulares ao seu médico ou a médicos especialistas. Somente um profissional médico é capaz de realizar diagnósticos bem como indicar tratamento. Nosso papel é apenas disponibilizar, em termos leigos, dados científicos considerados <strong>satisfatórios</strong> e acurados pela literatura médica.
        </Text>
        <Text>
          Diante destas considerações, gostaríamos que você lesse com atenção nossas condições para preenchimento da ferramenta:
        </Text>
        <Text>
          <ol>
            <li>
              O conteúdo disponibilizado no site da Previneo presta-se meramente à informação de conhecimentos médicos voltados ao cálculo da estimativa de risco de desenvolvimento de determinada patologia (câncer de mama, próstata, útero, cólon e pulmão).
            </li>
            <li>
              O preenchimento do formulário da Previneo não substitui a visita ao seu médico ou a médicos especialistas, nem pode ser entendido como diagnóstico médico ou prescrição de tratamento.
            </li>
            <li>
              Os formulários foram desenvolvidos de acordo com a literatura médica atualmente aceita pela comunidade médica nacional e internacional <strong>e seus resultados somente podem ser interpretados na data do preenchimento.</strong>
            </li>
            <li>
              Reserva-se o direito de alterar a composição do formulário e da forma de obtenção dos resultados se houver alteração da literatura médica atualmente aceita, <strong>o que será divulgado no próprio site.</strong>
            </li>
            <li>
              Em caso de alteração da metodologia, resultados anteriormente enviados não serão automaticamente atualizados. Isso dependerá de novo preenchimento do formulário.
            </li>
            <li>
              A alteração de metodologia será informada diretamente no site da Previneo.
            </li>
            <li>
              Os resultados serão traçados a partir das respostas fornecidas pelo usuário. Não nos responsabilizamos pelo preenchimento inadequado, equivocado ou incompleto das perguntas do questionário. Na hipótese de preenchimento incompleto, não será calculado o risco para desenvolvimento da patologia testada.
            </li>
            <li>
              A Previneo não se responsabiliza por erros de transmissão dos dados preenchidos. Nestes casos, será necessário novo preenchimento do formulário.
            </li>
            <li>
              <strong>O envio dos resultados será feito pelo meio escolhido pelo usuário, por correspondência eletrônica ou por telefone.</strong> Não nos responsabilizamos pelo preenchimento dos seus dados pessoais ou por erros na indicação de seus endereços para contato inclusive telefônico. Nestes casos, o envio do resultado ficará prejudicado. <strong>Não enviaremos resultados a outro endereço eletrônico ou forma de comunicação que não o constante do cadastro.</strong>
            </li>
            <li>
              A Previneo tem compromisso com a divulgação de informações médicas ao alcance do grande público. Por isso, manteremos sempre os melhores esforços no sentido de manter as informações do site atualizadas, precisas e completas o quanto possível.
            </li>
            <li>
              Ao preencher o formulário, o usuário declarará que leu e concorda com os presentes termos, especialmente no que diz respeito à compreensão do que seja uma ferramenta de cálculo de risco, e a possibilidade de obtenção de resultados falso positivo ou falso negativo.
            </li>
            <li>
              Em nenhuma hipótese a Previneo poderá ser responsabilizada pelos resultados obtidos no preenchimento do formulário.
            </li>
            <li>
              O conteúdo do site, e a marca da Previneo são de sua propriedade exclusiva e não podem ser utilizados por terceiros, sendo expressamente vedada a reprodução de quaisquer utilização sem autorização de todos os dados constantes do site. O usuário assume responsabilidade pela utilização indevida.
            </li>
            <li>
              Algumas ferramentas do site somente serão disponibilizadas mediante pagamento prévio. Nestes casos, o usuário será sempre previamente informado e realizar o pagamento como condição para utilização da ferramenta. Em hipótese alguma o usuário será cobrado pela utilização após a utilização da ferramenta.
            </li>
            <li>
              O site reserva-se o direito de iniciar a qualquer tempo a cobrança para utilização de qualquer ferramenta.
            </li>
            <li>
              A Previneo poderá a qualquer tempo interromper suas atividades, sem necessidade de aviso prévio.
            </li>
            <li>
              O site está naturalmente sujeito a problemas de falhas técnicas, interrupção ou indisponibilidade temporária, não havendo responsabilidade da Previneo nestes casos.
            </li>
            <li>
              O envio dos resultados dependerá da quantidade de fluxo de usuários e é por isso variável. Não nos comprometemos com a entrega de resultados em um prazo específico.
            </li>
            <li>
              Ao utilizar nossa ferramenta, o usuário concorda que foi informado e que tem conhecimento que em nenhuma hipótese o preenchimento de dados online substituirá a visita a um médico.
            </li>
            <li>
              A ferramenta de cálculo de risco não é uma ferramenta de diagnóstico ou de prescrição de tratamento, mas apenas de difusão de informação.
            </li>
            <li>
              Se o usuário estiver de acordo com nosso “Termos de Autorização”, os resultados obtidos pela aplicação do formulário comporão uma base de dados para estabelecimento de políticas de prevenção ao câncer.
            </li>
            <li>
              Todos os dados coletados serão mantidos em absoluto sigilo, inclusive na hipótese de utilização do resultado em forma estatística, que ocorrerá sem a identificação direta ou indireta do usuário.
            </li>
            <li>
              Ao concordar com as presentes condições, o usuário declara que leu e compreendeu todas as condições aqui constantes, em especial no que toca à necessidade de acompanhamento médico para realização de diagnóstico e tratamentos.
            </li>
            <li>
              Fazem parte destas condições a nossa “Política de Privacidade” bem como nosso “Guia rápido de Apresentação” e “Perguntas Frequentes”, os quais o usuário também declara ter lido, compreendido e aceito.
            </li>
            <li>
              O presente documento é composto tanto pelas condições estabelecidas como pelas preliminares explicativas prestadas neste documento.
            </li>
          </ol>
        </Text>
      </VerticalSpacer>
    </Section>
  )
}



