import React from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { isEmpty } from 'ramda'
import QuestionnaireScreen from 'questionnaire/components/QuestionnaireScreen'
import CategoryWizard from 'questionnaire/containers/CategoryWizard'
import routes from 'routes'
import getRoute from 'utils/getRoute'
import { getCategory, getPatientToken, getScriptHash } from 'store/ducks/questionnaire'

function CategoryScreen ({ categoria, patientToken: patient, scriptHash: script }) {
  if (isEmpty(categoria)) {
    const to = getRoute(routes.questionnaire.categories, { script, patient })
    return <Redirect to={to} />
  }

  return (
    <QuestionnaireScreen title={categoria.nome} color={categoria.cor}>
      <CategoryWizard category={categoria} color={categoria.cor} />
    </QuestionnaireScreen>
  )
}

const mapStateToProps = (state, { match: { params } }) => ({
  categoria: getCategory(state, parseInt(params.id, 10)),
  patientToken: getPatientToken(state),
  scriptHash: getScriptHash(state)
})

export default connect(
  mapStateToProps
)(CategoryScreen)



