import React from 'react'
import { connect } from 'react-redux'
import { Redirect, Link as RouterLink } from 'react-router-dom'
import VerticalSpacer from 'components/VerticalSpacer'
import Text from 'components/Text'
import Button from 'components/Button'
import Link from 'components/Link'
import * as duck from 'store/ducks/questionnaire'
import CategoryProgressButton from 'questionnaire/components/CategoryProgressButton'
import getRoute from 'utils/getRoute'
import routes from 'routes'
import theme from 'theme'

function Categories ({ data, patientToken, scriptHash, categories }) {
  const to = getRoute(routes.questionnaire.personalData, {
    patient: patientToken,
    script: scriptHash
  })

  if (!data.paciente.cadastro_completo) {
    return <Redirect to={to} />
  }

  const disabled = !!categories.find(c => c.progresso < 1)

  let questionnaireResult = (
    <Text size='18px'>
      As anamneses desta campanha não se adequam ao seu perfil. Obrigado por nos ajudar até aqui!
    </Text>
  )

  if (categories.length > 0) {
    questionnaireResult = (
      <div style={{textAlign: 'right'}}>
        <Button
          component={RouterLink}
          to={getRoute(routes.questionnaire.result, {
            patient: patientToken,
            script: scriptHash
          })}
          blockAtBreakpoint='small'
          onClick={e => disabled && e.preventDefault()}
          disabled={disabled}
          color={theme.colors.personal}
        >
        Finalizar avaliação
        </Button>
      </div>
    )
  }

  return (
    <VerticalSpacer>
      <VerticalSpacer space={15}>
        <Text size='18px'>
          <Link to={to}>Clique aqui</Link> para atualizar seus dados pessoais.
        </Text>
      </VerticalSpacer>
      <VerticalSpacer>
        {categories.map(categoria => (
          <CategoryProgressButton
            to={getRoute(routes.questionnaire.category, {
              patient: patientToken,
              script: scriptHash,
              id: categoria.id
            })}
            key={categoria.id}
            title={categoria.nome}
            image={categoria.tipo_cancer.imagem ? categoria.tipo_cancer.imagem.url : ''}
            value={categoria.progresso}
            color={categoria.cor}
          />
        ))}
        {questionnaireResult}
      </VerticalSpacer>
    </VerticalSpacer>
  )
}

const mapStateToProps = state => ({
  data: duck.getData(state),
  categories: duck.getCategories(state),
  patientToken: duck.getPatientToken(state),
  scriptHash: duck.getScriptHash(state)
})

const mapDispatchToProps = {

}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Categories)



