import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import VerticalSpacer from 'components/VerticalSpacer'
import Button from 'components/Button'
import PatientWizard from 'questionnaire/components/PatientWizard'
import * as duck from 'store/ducks/questionnaire'
import getRoute from 'utils/getRoute'
import routes from 'routes'

function PatientPersonalData (props) {
  const to = getRoute(routes.questionnaire.anamnese, {
    patient: props.patientToken,
    script: props.scriptHash
  })

  return (
    <VerticalSpacer>
      {props.data.paciente.cadastro_completo && (
        <Button to={to} component={Link} size='small' color='personal'>
          Voltar à anamnese
        </Button>
      )}
      <PatientWizard
        initialValues={props.data.paciente}
        scriptFormFields={props.data.roteiro.form_fields}
        onSubmit={data => {
          props.updatePatient({
            scriptHash: props.scriptHash,
            patientId: data.id,
            data
          })
        }}
        isSubmitting={props.isSubmitting}
        redirectTo={to}
        error={props.error}
      />
    </VerticalSpacer>
  )
}

const mapStateToProps = state => ({
  data: duck.getData(state),
  isSubmitting: duck.isSubmitting(state),
  patientToken: duck.getPatientToken(state),
  scriptHash: duck.getScriptHash(state),
  error: duck.getError(state)
})

const mapDispatchToProps = {
  updatePatient: duck.updatePatient
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PatientPersonalData)



