import React from 'react'
import { Field } from 'react-final-form'
import * as forms from 'utils/forms'
import CustomField from 'components/CustomField'
import FormGrid from 'components/FormGrid'
import WithModal from 'admin/components/WithModal'
import Link from 'components/Link'
import Terms from 'questionnaire/components/Terms'
import CustomQuestionField from 'questionnaire/components/CustomQuestionField'
import CustomFieldSelect from 'admin/components/CustomFieldSelect'
import Resource from 'containers/Resource'
import FormAddress from 'admin/forms/FormAddress'

export const formPersonal = (
  <React.Fragment>
    <Field
      name='name'
      component={CustomField}
      label='Nome'
      {...forms.required}
    />
    <Field
      name='cpf'
      component={CustomField}
      label='CPF'
      {...forms.cpfRequired}
    />
    <Field
      name='email'
      component={CustomField}
      label='E-mail'
      {...forms.emailRequired}
    />
  </React.Fragment>
)

export const formAddress = props =>
  <FormAddress {...props} />

export const formPhysical = (
  <React.Fragment>
    <FormGrid>
      <Field
        name='altura'
        type='number'
        min={60}
        max={250}
        component={CustomField}
        label='Altura (cm)'
        tooltip='Insira sua altura em centímetros (cm)'
        {...forms.integerRequired}
      />
      <Field
        name='peso'
        type='number'
        component={CustomField}
        label='Peso (kg)'
        min={30}
        max={300}
        tooltip='Insira seu peso em kilogramas (kg)'
        {...forms.integerRequired}
      />
    </FormGrid>
    <FormGrid>
      <Field
        name='data_nascimento'
        component={CustomField}
        label='Data de nascimento'
        tooltip='A data deve estar no formado DD/MM/AAAA (exemplo: 10/10/1950'
        {...forms.dateRequired}
      />
      <Field
        name='sexo'
        component={CustomField.Select}
        label='Sexo'
        {...forms.required}
      >
        <option value='' disabled>Selecione</option>
        <option value='M'>Masculino</option>
        <option value='F'>Feminino</option>
      </Field>
    </FormGrid>
    <Field
      name='aceita_contato'
      component={CustomField.Option}
      type='checkbox'
    >
      Eu aceito contato via telefone ou e-mail
    </Field>
    <WithModal modal={Terms}>
      {({ toggleModal }) => (
        <Field
          name='termos_uso'
          component={CustomField.Option}
          type='checkbox'
          validate={v => !v && 'É necessário aceitar os termos'}
        >
          Eu aceito os
          {' '}
          <Link
            component='button'
            onClick={e => {
              e.preventDefault()
              toggleModal()
            }}
          >
            Termos de Serviço
          </Link>
        </Field>
      )}
    </WithModal>
  </React.Fragment>
)

export const formExam = (
  <React.Fragment>
    <Field
      name='brca'
      component={CustomQuestionField}
      question='Você já realizou algum exame genético, para saber se tem uma alteração/mutação que aumenta seu risco para desenvolver câncer? Esse exame é conhecido como BRCA1 ou BRCA2.'
      description='A mutação nos genes BRCA1 ou BRCA2 aumentam significativamente o risco para alguns tipos de câncer (mama, ovário, próstata, cólon e pâncreas). Esse é um exame genético que pode ser feito pelo sangue ou pela saliva para a pessoa saber se apresenta a mutação. Essa mutação ocorre geralmente em pessoas que têm vários familiares próximos acometidos por câncer. Estima-se que aproximadamente 5% a 10% dos casos de câncer de mama e ovários são ocasionados por essa mutação'
      color='personal'
      options={[
        { label: 'Sim', value: 'Sim' },
        { label: 'Não', value: 'Não' },
        { label: 'Não Sei', value: 'Não Sei' }
      ]}
      {...forms.required}
    />
  </React.Fragment>
)

export const formEthnicity = (
  <React.Fragment>
    <Field
      name='etnia'
      component={CustomQuestionField}
      question='Etnia'
      description='Sabe-se que pessoas de etnia negra apresentam risco aumentado para desenvolver câncer de próstata.'
      options={[
        { label: 'Branco', value: 'Branco' },
        { label: 'Negro', value: 'Negro' },
        { label: 'Pardo', value: 'Pardo' },
        { label: 'Amarelo', value: 'Amarelo' },
        { label: 'Indígena', value: 'Indígena' }
      ]}
      {...forms.required}
    />
  </React.Fragment>
)

export const formHistory = ({ values }) => (
  <React.Fragment>
    <Field
      name='historico_cancer'
      component={CustomQuestionField}
      question='Possui histórico de câncer?'
      description='Sabe-se que quem já foi acometido por câncer apresenta um risco elevado de apresentar recidiva ou eventualmente um novo tumor do mesmo tipo. Por isso é importante para o seu cálculo de risco saber se você já foi acometido por essa doença em sua vida.'
      color='personal'
      options={[
        { label: 'Sim', value: true },
        { label: 'Não', value: false }
      ]}
      {...forms.required}
    />
    {values.historico_cancer && (
      <React.Fragment>
        <Field
          name='historico_tipo_cancer'
          component={CustomQuestionField}
          question='Qual o tipo de câncer'
          color='personal'
          options={[
            { label: 'Próstata', value: 'prostata' },
            { label: 'Pulmão', value: 'pulmao' },
            { label: 'Cólon', value: 'colon' },
            { label: 'Mama', value: 'mama' },
            { label: 'Útero', value: 'utero' },
            { label: 'Outro', value: 'outro' }
          ]}
          {...forms.required}
        />
        {values.historico_tipo_cancer === 'outro' && (
          <Field
            name='historico_tipo_cancer_outro'
            component={CustomField}
            label='Outro: digite sua resposta'
            id='historico_tipo_cancer_outro'
            {...forms.required}
          />
        )}
      </React.Fragment>
    )}
  </React.Fragment>
)

export const formPhone = (
  <Field
    key='form_telefone'
    name='telefone'
    label='Telefone'
    id='form_patient_form_fields_phone'
    component={CustomField}
    {...forms.phoneRequired}
  />
)

export const formCode = (
  <Field
    key='form_matricula'
    name='matricula'
    label='Matrícula'
    id='form_patient_form_fields_code'
    component={CustomField}
    {...forms.required}
  />
)

export const formHealthInsurance = (
  <Resource key='form_plano_saude' resource='HealthInsurances' autoFetch>
    {props => (
      <Field
        name='plano_saude.id'
        label='Plano de Saúde'
        id='form_patient_plano_de_saude'
        component={CustomFieldSelect}
        customSelectProps={{
          options: props.records,
          labelKey: 'nome',
          valueKey: 'id',
          isLoading: props.isFetching
        }}
      />
    )}
  </Resource>
)



