import React from 'react'
import { isEmpty } from 'ramda'
import { Link } from 'react-router-dom'
import SectionTitle from 'components/SectionTitle'
import Image from 'components/Image'
import VerticalSpacer from 'components/VerticalSpacer'
import Text from 'components/Text'
import Button from 'components/Button'
import Footer from 'components/Footer'
import Resource from 'containers/Resource'
import getRoute from 'utils/getRoute'
import routes from 'routes'
import { Container, Customer } from './styles'

export default function CompanyScreen (props) {
  return (
    <Resource
      resource='AnamneseCompany'
      id={props.match.params.slug}
      autoFetch
      spinner
    >
      {({ detailedRecord }) => {
        if (isEmpty(detailedRecord)) {
          window.location.href = '/'
          return
        }
        return (
          <React.Fragment>
            <Container>
              <Image src={detailedRecord.roteiro.empresa.logo.url} alt='Logo' style={{marginBottom: 50}} />
              <VerticalSpacer space={40}>
                <SectionTitle
                  size='45px'
                  color='text'
                  style={{fontWeight: 'normal'}}
                >
                  Você se preocupa com a sua saúde, não é?<br />
                  A <Customer>{detailedRecord.roteiro.empresa.nome}</Customer> também!
                </SectionTitle>
                <Text size='18px' lineHeight={2}>
                  Nós também acreditamos que a prevenção é sempre o melhor tratamento.
                </Text>
                <Text size='18px' lineHeight={2}>
                  Por isso, estamos lançando um programa que consiste no rastreamento e diagnóstico dos principais tipos de câncer. Através de um sistema pioneiro e extremamente inteligente, colheremos algumas informações para estratificar o seu risco para desenvolvimento dos 5 principais tipos de câncer:
                </Text>
                <Text
                  size='18px'
                  lineHeight={2}
                  className='list'
                  component='div'
                >
                  <ul>
                    <li>Câncer de Mama</li>
                    <li>Câncer de Próstata</li>
                    <li>Câncer de Colo de Útero</li>
                    <li>Câncer de Cólon (intestino)</li>
                    <li>Câncer de Pulmão</li>
                  </ul>
                  Esses 5 tipos de câncer são responsáveis por quase 70% de todos os tipos de câncer e têm uma coisa em comum: <strong>PODEM SER DETECTADOS PRECOCEMENTE!</strong>
                </Text>
                <SectionTitle
                  size='45px'
                  color='text'
                  style={{ fontWeight: 'normal' }}
                >
                  Basta preencher a anamnese a seguir.
                </SectionTitle>
                <Text size='18px' lineHeight={2}>
                  Os dados fornecidos por você são absolutamente sigilosos e serão usados para analisar seu risco de desenvolvimento de algum câncer. Após análise da PreviNeo, daremos um retorno aos colaboradores para que as medidas adequadas de prevenção sejam tomadas.
                </Text>
                <Text size='18px' lineHeight={2}>
                  Então, vamos lá?
                </Text>
                <Button
                  component={Link}
                  to={getRoute(routes.questionnaire.companyPatient, {
                    companyId: detailedRecord.roteiro.empresa.id,
                    hash: detailedRecord.hash
                  })}
                  arrow
                  block
                >
                  Iniciar prevenção
                </Button>
              </VerticalSpacer>
            </Container>
            <Footer />
          </React.Fragment>
        )
      }}
    </Resource>
  )
}



