import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { isEmpty } from 'ramda'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { Field } from 'react-final-form'
import Wizard from 'questionnaire/components/Wizard'
import CustomQuestionField from 'questionnaire/components/CustomQuestionField'
import * as duck from 'store/ducks/questionnaire'
import { required } from 'utils/forms'
import getRoute from 'utils/getRoute'
import routes from 'routes'
import { SCRIPT_NODE_TYPES, TYPE_QUESTIONS } from 'config/constants'
import Text from 'components/Text'
import Button from 'components/Button'
import CategoryProgressButton from 'questionnaire/components/CategoryProgressButton'

class CategoryWizard extends Component {
  static propTypes = {
    /** Categoria */
    category: PropTypes.object.isRequired
  }

  rootQuestionsId = this.props.category.script.map(q => q.id)

  state = {
    currentQuestion: this.props.category.script[0],
    parent: { children: this.props.category.script, parent: null },
    history: [],
    done: false
  }

  componentDidUpdate (prevProps) {
    if (prevProps.isSubmitting && !this.props.isSubmitting && isEmpty(this.props.error)) {
      this.nextQuestion()
    }
  }

  nextQuestion () {
    const { lastAnswer: answer, currentQuestion: question, parent } = this.state

    if (answer.children && answer.children.length > 0) {
      return this.setState({
        currentQuestion: answer.children[0],
        parent: {
          children: answer.children,
          parent: {
            ...question,
            parent
          }
        },
        history: this.state.history.concat({ question, parent })
      })
    }

    const nextQuestion = this.getNextQuestion(question, parent)

    if (nextQuestion) {
      this.setState({
        currentQuestion: nextQuestion.question,
        parent: nextQuestion.parent,
        history: this.state.history.concat({ question, parent })
      })
    } else {
      this.setState({ done: true })
    }
  }

  getNextQuestion (question, parent) {
    // Verifica se o question possui um irmão
    const questionIndex = parent.children.findIndex(q => q.id === question.id)

    if (typeof questionIndex === 'number' && parent.children[questionIndex + 1] && parent.type !== SCRIPT_NODE_TYPES.QUESTION) {
      return {
        question: parent.children[questionIndex + 1],
        parent
      }
    }

    if (parent.parent) {
      return this.getNextQuestion(parent, parent.parent)
    }
  }

  previousQuestion () {
    if (this.state.history.length > 0) {
      const lastItem = this.state.history[this.state.history.length - 1]
      this.setState({
        currentQuestion: lastItem.question,
        parent: lastItem.parent,
        history: this.state.history.slice(0, this.state.history.length - 1)
      })
    }
  }

  handleSubmit = values => {
    const { currentQuestion: question } = this.state
    const answer = this.state.currentQuestion.typeQuestion === TYPE_QUESTIONS.OBJETIVA.id
      ? this.state.currentQuestion.children.find(a => a.id === values[`question-${question.id}`])
      : { value: values[`question-${question.id}`] }
    this.setState({
      lastAnswer: answer
    }, () => this.props.answerQuestion({
      patientToken: this.props.patientToken,
      scriptHash: this.props.scriptHash,
      question: {
        id: question.id,
        answer: answer.id || answer.value
      }
    }))
  }

  handleBackClick = () => {
    this.previousQuestion()
  }

  canGoBack () {
    return this.state.currentQuestion.id !== this.props.category.script[0].id
  }

  getProgress () {
    const answeredRootQuestions = this.state.history
      .map(obj => obj.question.id)
      .filter(id => this.rootQuestionsId.includes(id))

    return answeredRootQuestions.length
  }

  getInitialValues () {
    const { perguntasRespondidas } = this.props
    const { currentQuestion: question } = this.state

    if (!perguntasRespondidas.hasOwnProperty(question.id)) {
      return {}
    }

    const pergunta = perguntasRespondidas[question.id]
    const resposta = pergunta.resposta[0]

    const value = pergunta.tipo_resposta === TYPE_QUESTIONS.OBJETIVA.id
      ? resposta.id
      : resposta.resposta

    return {
      [`question-${question.id}`]: value
    }
  }

  renderCurrentForm () {
    const { currentQuestion: question } = this.state
    const options = question.typeQuestion === TYPE_QUESTIONS.OBJETIVA.id
      ? (
        question.children.map(child => ({
          label: child.title,
          value: child.id
        }))
      ) : null

    return (
      <div>
        <Field
          name={`question-${question.id}`}
          component={CustomQuestionField}
          question={question.title}
          description={question.description}
          options={options}
          color={this.props.color}
          {...required}
        />
      </div>
    )
  }

  render () {
    if (this.state.done) {
      const to = getRoute(routes.questionnaire.categories, {
        patient: this.props.patientToken,
        script: this.props.scriptHash
      })

      const category = this.props.category

      return (
        <React.Fragment>
          <Text
            size='25px'
            lineHeight={1}
            style={{ marginBottom: '15px' }}
            color={category.cor}
          >
            <strong>Mais uma etapa concluída!</strong>
          </Text>
          <Text size='18px' style={{marginBottom: 50}}>
            Você está preenchendo os dados da anamnese de maneira correta.<br />Continue assim!
          </Text>
          <CategoryProgressButton
            to={to}
            key={category.id}
            title={category.nome}
            image={category.tipo_cancer.imagem ? category.tipo_cancer.imagem.url : ''}
            value={1}
            color={category.cor}
          />
          <Button
            to={to}
            color={category.cor}
            style={{ width: '100%', marginTop: '50px' }}
            component={Link}
          >
            Continuar avaliação
          </Button>
        </React.Fragment>
      )
    }

    return (
      <Wizard
        initialValues={this.getInitialValues()}
        form={this.renderCurrentForm()}
        onSubmit={this.handleSubmit}
        canGoBack={this.canGoBack()}
        onBackClick={this.handleBackClick}
        isSubmitting={this.props.isSubmitting}
        count={this.rootQuestionsId.length}
        progress={this.getProgress()}
        color={this.props.color}
      />
    )
  }
}

const mapStateToProps = state => ({
  perguntasRespondidas: duck.getData(state).paciente.perguntas_respondidas,
  patientToken: duck.getPatientToken(state),
  scriptHash: duck.getScriptHash(state),
  isSubmitting: duck.isSubmitting(state),
  error: duck.getError(state)
})

const mapDispatchToProps = {
  answerQuestion: duck.answerQuestion
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CategoryWizard)



