import React from 'react'
import PropTypes from 'prop-types'
import Option from 'components/Option'
import VerticalSpacer from 'components/VerticalSpacer'
import Input from 'components/Input'
import LargeOption from './LargeOption'
import { Question, LargeOptionsContainer, Description } from './styles'

export default function QuestionField ({ question, options, description, value, onChange, ...props }) {
  return (
    <div>
      <Question>{question}</Question>
      {options ? (
        options.length > 3 ? (
          <VerticalSpacer space={10}>
            {options.map(option => (
              <Option
                key={option.value}
                type='radio'
                children={option.label}
                value={option.value}
                checked={value === option.value}
                onChange={() => onChange(option.value)}
              />
            ))}
          </VerticalSpacer>
        ) : (
          <LargeOptionsContainer>
            {options.map(option => (
              <LargeOption
                key={option.value}
                label={option.label}
                value={option.value}
                checked={value === option.value}
                onChange={onChange}
                color={props.color ? props.color : 'primary'}
              />
            ))}
          </LargeOptionsContainer>
        )
      ) : (
        <Input
          value={value}
          onChange={onChange}
        />
      )}
      {description && (
        <Description>{description}</Description>
      )}
    </div>
  )
}

QuestionField.propTypes = {
  /** Pergunta */
  question: PropTypes.string.isRequired,

  /** Lista de opções */
  options: PropTypes.array,

  /** Descrição opcional */
  description: PropTypes.string,

  /** Valor atual */
  value: PropTypes.any,

  /** Handler quando o valor for alterado */
  onChange: PropTypes.func.isRequired
}



